import { Component, OnDestroy, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { ComponentProps, ComponentRef } from '@ionic/core';

import { Subscription } from 'rxjs';

import { ImovelDto } from './../../shared/interfaces/imoveis/imovel.dto';
import { VeiculoDto } from './../../shared/interfaces/veiculos/veiculo.dto';
import { UserDto } from './../../shared/interfaces/users/user.dto';
import { PageableDto } from './../../shared/interfaces/others/pageable.dto';
import { PageOrder } from './../../shared/constants/page.constant';
import { TipoUsuarioEnum } from './../../shared/constants/tipo-user.constant';
import { VeiculosService } from './../../shared/services/veiculos/veiculos.service';
import { ImovelService } from './../../shared/services/imovel/imovel.service';
import { UserService } from './../../shared/services/user/user.service';
import { ConsultaUserComponent } from './../../shared/components/consulta-rapida/consulta-user/consulta-user.component';

@Component({
  selector: 'app-consulta-rapida',
  templateUrl: './consulta-rapida.page.html',
  styleUrls: ['./consulta-rapida.page.scss'],
})
export class ConsultaRapidaPage implements OnInit, OnDestroy {

  searchedUser = '';
  users: PageableDto<UserDto>;

  searchedVeiculo = '';
  veiculos: PageableDto<VeiculoDto>;

  searchedImovel = '';
  imoveis: PageableDto<ImovelDto>;

  hasSearched = false;

  private subscriptions = new Subscription();

  constructor(
    private readonly usersService: UserService,
    private readonly imovelService: ImovelService,
    private readonly veiculosService: VeiculosService,
    private readonly modal: ModalController,
  ) { }

  ngOnInit() {
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }


  //#region USERS
  searchUser(page = 1) {
    this.hasSearched = true;
    const sub = this.usersService.listUsers(
      {
        searchedUser: this.searchedUser,
        tipo_usuario: TipoUsuarioEnum.USUARIO,
      },
      { order: PageOrder.DESC, page, take: 10 }
    )
      .subscribe(response => {
        this.users = response;
      });

    this.subscriptions.add(sub);
  }

  openUser(user: UserDto) {
    const { responsavel } = user;

    if (responsavel && responsavel.length >= 1) {
      this.showModal(ConsultaUserComponent, { user_id: responsavel[0].user_responsavel.id });
    } else {
      this.showModal(ConsultaUserComponent, { user_id: user.id });
    }
  }

  //#endregion


  //#region VEICULOS

  searchVeiculo(page = 1) {
    this.hasSearched = true;
    const sub = this.veiculosService.list(
      {
        searched_veiculo: this.searchedVeiculo,
      },
      { order: PageOrder.DESC, page, take: 10 }
    )
      .subscribe(response => {
        this.veiculos = response;
      });

    this.subscriptions.add(sub);
  }

  openVeiculo(veiculo: VeiculoDto) {
    const { id } = veiculo.responsavel;
    this.showModal(ConsultaUserComponent, { user_id: id });
  }
  //#endregion

  hasItem(page: PageableDto<any>) {
    if (page && page.meta && page.meta.itemCount >= 1) {
      return true;
    }

    return false;
  }

  private async showModal(
    component: ComponentRef,
    componentProps?: ComponentProps
  ) {
    const modal = await this.modal.create({
      component,
      backdropDismiss: false,
      cssClass: 'modal-size-100',
      componentProps,
    });

    modal.onDidDismiss().then((data) => {
    });

    return await modal.present();
  }
}
