import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { Subscription } from 'rxjs';

import { UserService } from './../../../services/user/user.service';
import { UserDto } from './../../../interfaces/users/user.dto';
import { ImovelDto } from './../../../interfaces/imoveis/imovel.dto';


@Component({
  selector: 'app-consulta-user',
  templateUrl: './consulta-user.component.html',
  styleUrls: ['./consulta-user.component.scss'],
})
export class ConsultaUserComponent implements OnInit, OnDestroy {

  @Input()
  user_id: string;

  user: UserDto;
  private subscriptions = new Subscription();

  constructor(
    private readonly usersService: UserService,
    private readonly modalCtrl: ModalController,
  ) { }

  ngOnInit() {
    const sub = this.usersService.findById(this.user_id)
      .subscribe((response) => {
        this.user = response;
        console.log('Computado');
        console.log(this.user);
      });

    this.subscriptions.add(sub);
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  getImoveisProprietario(): ImovelDto[] {
    const { preponente1, preponente2, preponente3 } = this.user;
    const propriedades = [].concat(...preponente1, preponente2, preponente3) as ImovelDto[];

    return propriedades;
  }

  async fechar() {
    await this.modalCtrl.dismiss();
  }
}
